import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import Login from "./Login";
import SignUp from "./SignUp";
const Header = () => {
  const [loginOn, setLoginOn] = useState(false);
  const [signOn, setSignOn] = useState(false);
  const [input, setInput] = useState("");
  const [on, setOn] = useState(false);
  const [LoginOrLogout, setLoginOrLogout] = useState("");

  useEffect(() => {
    const ACCESS_TOKEN = window.localStorage.getItem("ACCESS_TOKEN");
    if (ACCESS_TOKEN && ACCESS_TOKEN != "null") {
      setLoginOrLogout("");
    } else {
      setLoginOrLogout("");
    }
  }, []);
  const submitLogout = () => {
    window.localStorage.setItem("ACCESS_TOKEN", null);
    document.location.href = "/";
  };
  return (
    <>
      <header className="Header">
        <div className="inner">
          <div className="head_left">
            <h1>
              <Link to="/">
                <img src={"/img/main_img/eruty_logo2.png"} alt="" />
              </Link>
            </h1>
            <ul className="web_menu">
              <li>
                <Link to={`#!`}></Link>
              </li>
              <li>
                <Link to={`#!`}></Link>
              </li>
              <li>
                <Link to={`#!`}></Link>
              </li>
            </ul>
          </div>
          <div className="head_center">
            <div className="search">
              <form action="">
                <input
                  type="text"
                  value={input}
                  placeholder=" "
                  onFocus={() => {
                    setInput("");
                  }}
                  onChange={(e) => {
                    setInput(e.target.value);
                  }}
                />
                <button>
                  <i>
                    <img src={"img/main_img/search.svg"} alt="" />
                  </i>
                </button>
              </form>
              <div className="search_word">
                <span>
                  <Link to={`#!`}></Link>
                </span>
                <span>
                  <Link to={`#!`}></Link>
                </span>
                <span>
                  <Link to={`#!`}></Link>
                </span>
                <span>
                  <Link to={`#!`}></Link>
                </span>
                <span>
                  <Link to={`#!`}>HDRI</Link>
                </span>
              </div>
            </div>
            <span
              className={`m_open ${on ? "on" : ""}`}
              onClick={() => {
                setOn(!on);
              }}
            >
              <button>mopen</button>
            </span>
          </div>
          <div className={`head_right ${on ? "on" : ""}`}>
            <ul className="member">
              <li>
                <Link
                  onClick={() =>
                    LoginOrLogout == ""
                      ? setLoginOn(true)
                      : submitLogout()
                  }
                >
                  {LoginOrLogout}
                </Link>
              </li>
              <li>
                <Link
                  onClick={() => {
                    setSignOn(true);
                  }}
                >
                  
                </Link>
              </li>
            </ul>
            <ul className="icon_menu">
              <li>
                <Link to={`#!`}>
                  <i>
                    <img src={`/img/main_img/h_right01.svg`} alt="" />
                  </i>
                </Link>
              </li>
              <li>
                <Link to={`#!`}>
                  <i>
                    <img src={`/img/main_img/h_right02.svg`} alt="" />
                  </i>
                </Link>
              </li>
              <li>
                <Link to="/MyPage">
                  <i>
                    <img src={`/img/main_img/h_right03.svg`} alt="" />
                  </i>
                </Link>
              </li>
            </ul>
            <ul className="mobile_menu">
              <li>
                <Link to={`#!`}></Link>
              </li>
              <li>
                <Link to={`#!`}></Link>
              </li>
              <li>
                <Link to={`#!`}></Link>
              </li>
            </ul>
          </div>
        </div>
      </header>
      {loginOn && (
        <Login
          loginOn={loginOn}
          setLoginOn={setLoginOn}
          setSignOn={setSignOn}
        />
      )}
      {signOn && (
        <SignUp
          loginOn={loginOn}
          setLoginOn={setLoginOn}
          signOn={signOn}
          setSignOn={setSignOn}
        />
      )}
    </>
  );
};

export default Header;
